.home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  color: #333333;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 30px;
}

.home-content {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 20px 20px;
  padding: 10px 20px;
  /* box-shadow: 0px 0px 1px black; */
}

.youtube-wrapper{
  margin: 1px;
}


@media (max-width: 450px){
  .home{
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-top: 60px;
    padding-top: 0px;
    overflow-y: scroll;
  }
}


